/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body {
  box-sizing: border-box; }

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/* Header */
.change-intro-webpage {
  height: 25vh; }
  .change-intro-webpage .change-nyt-logo {
    width: 75px;
    height: 75px; }

html, body {
  background-color: #000000;
  color: #FFFFFF;
  box-sizing: border-box;
  font-size: 16px;
  max-width: 100%; }
  html img, body img {
    max-width: 100%;
    max-height: 100%; }

/* Header */
.intro-webpage {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 85vh; }
  .intro-webpage .nyt-logo {
    width: 225px;
    height: 225px; }
  .intro-webpage p {
    padding: 15px 0; }
  .intro-webpage .dropdown-section {
    border-radius: 5px;
    border: none;
    padding: 1.5px;
    text-align: center; }
  .intro-webpage .preload {
    display: none;
    align-self: center;
    width: 37.5px;
    height: 37.5px;
    padding-top: 15px; }

/* Body */
section {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-auto-rows: minmax(1, 12); }

li a, li a:link, li a:visited {
  text-decoration: none;
  color: #FFFFFF; }
  li a .image-content, li a:link .image-content, li a:visited .image-content {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    width: 100vw;
    height: 350px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat; }
    li a .image-content .text-content, li a:link .image-content .text-content, li a:visited .image-content .text-content {
      background-color: rgba(0, 0, 0, 0.5);
      padding: 10px 5px;
      line-height: 25px; }

/* Footer */
footer {
  text-align: center; }
  footer p {
    padding: 37.5px 0; }
